<template>
  <div id="clock" class="clock">
    <p class="date">{{ date+" "+time }}</p>
    <!-- <p class="time">{{  }}</p> -->
  </div>
</template>
<script>
export default {
  data() {
    return {
      week: [
        "Domingo",
        "Lunes",
        "Martes",
        "Miércoles",
        "Jueves",
        "Viernes",
        "Sábado",
      ],
      time: "",
      date: "",
    };
  },
  created() {
    this.updat()
  },
  methods: {
     updat() {
      var that = this;
      setTimeout(function () {
        // ...
        that.updateTime();
        // ...
      }, 0);
    },
    updateTime() {
          setTimeout(() => {
      var cd = new Date();
      this.time =
        this.zeroPadding(cd.getHours(), 2) +
        ":" +
        this.zeroPadding(cd.getMinutes(), 2) +
        ":" +
        this.zeroPadding(cd.getSeconds(), 2);
      this.date =
        this.zeroPadding(cd.getFullYear(), 4) +
        "-" +
        this.zeroPadding(cd.getMonth() + 1, 2) +
        "-" +
        this.zeroPadding(cd.getDate(), 2) +
        " " +
        this.week[cd.getDay()];
        this.updateTime()
          },1000)
    },
    zeroPadding(num, digit) {
      var zero = "";
      for (var i = 0; i < digit; i++) {
        zero += "0";
      }
      return (zero + num).slice(-digit);
    },
  },
};
</script>
