            return this.productlst
        },
        total(){
            if(this.productlst==[])
            return 0
            console.log(this.productlst.map(s=>{return (s.val*s.count)}))
            return this.formatnum(this.productlst.map(s=>{return (s.val*s.count)}).reduce((prev, curr) => prev + curr, 0))
        },
        subtotal(){
            if(this.productlst==[])
            return 0
            return this.formatnum(this.productlst.map((s)=>{return (s.val*s.count)}).reduce((prev, curr) => prev + curr, 0))
        }
    },
    methods: {
        formatnum(num){
            var formatter = new Intl.NumberFormat('es-CO'
            // , {}
            )
        return formatter.format(num)
        }
        ,
       remove () {
        this.product=null
      },
      addprod(){
          if(this.product!=null)
          {
              if(this.productlst!=[])
              {
                  if(this.productlst.some(s=>s.cod==this.product))
                  {
                      this.productlst.filter(s=>s.cod==this.product).forEach(s=>s.count=s.count+1)
                      this.product=null
                  }
                  else
                  {
                      let toadd=this.people.find(t=>t.cod==this.product)
                      toadd.count=1
                      this.productlst.push(toadd)
                      this.product=null
                  }
              }
          }
      },
     
