          count: 0,
        },
        {
          cod: "7708544295359",
          name: "DETERGENTE LIQUIDO",
          desc: "Detergente Líquido. Máxima Limpieza. Protege sus prendas. ",
          val: 13500,
          img: require("@/assets/products/7708544295359.png"),
