          name: "ANTENA UHF MINIMALISTA",
          desc:
            "Antena UHF Minimalista. Especificaciones: Frecuencia de Operación: UHF:470 - 862 MHz Ganancia: 2 a 4 dB Nivel de Salida: 100 dB",
