      ],
      redeban: { title: "1", src: require("@/assets/red.png"), flex: 6 },
      datafono: { title: "2", src: require("@/assets/datafono.png"), flex: 6 },
    };
  },
  methods: {
    formatnum(num) {
      var formatter = new Intl.NumberFormat("es-CO");
      return formatter.format(num);
    },
    remove() {
      this.product = null;
    },
    addprod() {
      if (this.product != "") {
        if (this.productlst != []) {
          if (this.productlst.some((s) => s.cod == this.product)) {
            this.productlst
              .filter((s) => s.cod == this.product)
              .forEach((s) => (s.count = s.count + 1));
            this.product = "";
          } else {
            let toadd = this.people.find((t) => t.cod == this.product);
            toadd.count = 1;
            this.productlst.push(toadd);
            this.product = "";
          }
        }
      }
    },
  },
  computed: {
    productSelect() {
      //  if(this.product)
      //  return this.emptyProduct
      const prod = this.people.find(
        (s) => s.cod.toLowerCase() == this.product.toLowerCase()
      );
      if ((prod == null) | (prod == undefined)) return this.emptyProduct;
      return prod;
    },
    productlstcomp() {
      return this.productlst;
    },
    total() {
      if (this.productlst == []) return 0;
      console.log(
        this.productlst.map((s) => {
          return s.val * s.count;
        })
      );
      return this.formatnum(
        this.productlst
          .map((s) => {
            return s.val * s.count;
          })
          .reduce((prev, curr) => prev + curr, 0)
      );
    },
    subtotal() {
      if (this.productlst == []) return 0;
      return this.formatnum(
        this.productlst
          .map((s) => {
            return s.val * s.count;
          })
          .reduce((prev, curr) => prev + curr, 0)
      );
    },
  },
  watch: {
      overlay (val) {
        val && setTimeout(() => {
          this.overlay = false
          this.product=""
          this.productlst=[]
          
        }, 3000)
      },
    },
};
</script>
